const SERVER_URL = process.env.REACT_APP_SERVER_URL;




function ClientSideRules(props) {
    
    
    const[alert, setAlert] = useState(false);
    const[agree,SetAgree] = useState(false);

    const refresh = ()=>{
        setAlert(true)
        
    }

    return(
        <Container style={{backgroundColor:'black', color:'white',  paddingLeft:'20px', paddingRight:'20px'}} fluid>

  <Row>
    <Col>
    
    <div>
               <h1 >Bine ai venit la <Image style={{margin:'auto', width:'150px'}} src={logo} fluid /> </h1>
               <span>
               <div>
        
        <p><span style={{fontWeight: 400}}>Sala noastr de aventur indoor cu trasee de crat pentru aduli i copii.</span></p>
        
        <p><strong>Climb House</strong><span style={{fontWeight: 400}}> este construit dup un concept care a luat natere n vest. Ne propunem s aducem cratul i sportul, mai aproape de oraele mari i s oferim o alternativ sportiv i distractiv de petrecere a timpului liber.&nbsp;</span></p>
        
        <p><span style={{fontWeight: 400}}>Avem o experien de peste 10 ani n alpinism utilitar i lucrul la nlime i am mai construit astfel de centre n Germania, Anglia, Scoia, Irlanda&nbsp;i Finlanda.</span></p>
        
        <p><span style={{fontWeight: 400}}>Echipamentele de autoasigurare sunt achizitionate din UK de la un productor autorizat cu o experien de peste 25 de ani.&nbsp;</span><a href="https://safedownautobelay.com/about-us/"><span style={{fontWeight: 400}}>https://safedownautobelay.com/about-us/</span></a><span style={{fontWeight: 400}}>.&nbsp;</span></p>
        <p><span style={{fontWeight: 400}}>Acestea sunt anual verificate de o echip de experi Safedown&nbsp;dup un protocol strict.&nbsp;</span></p>
        
        <p><strong>Care sunt masurile de sigurant adoptate pentru activitatea de crare?</strong></p>
        <ul>
          <ul>
            <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>avem stabilit un maxim de 20 de persoane la 16 trasee de catarat&nbsp;i un slide</span></li>
            <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>lucram cu cele mai sigure echipamente de autoasigurare din lume</span></li>
            <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>sistemele de autoasigurare sunt asigurate&nbsp;n 2 puncte&nbsp;</span></li>
            <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>carabiniera de prindere este 3 lock adic necesit 3 pasi pt&nbsp;deblocare evitndu-se astfel complet deblocrile accidentale</span></li>
          </ul>
        </ul>
        
        <ul>
          <li><strong><span style={{fontWeight: 400}}>accesorioile de escalad sunt certificate UIAA&nbsp;(</span><strong>International Climbing and Mountaineering Federation)</strong></strong></li>
        </ul>
        
        <ul>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>podeaua spaiului&nbsp;este n 2 culori, delimitndu-se astfel zonele de trecere i zonele care trebuie evitate cnd sunt crtori pe traseu</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>fiecare traseu este prevzut cu un sistem de blocare, astfel nct crtorii nu pot pleca pe traseu pn nu se asigur.</span></li>
        </ul>
        
        <p><span style={{fontWeight: 400}}>&nbsp;Traseele noastre de crat sunt accesibile tuturor persoanelor ncepand cu varsta de 4-5 ani, care au peste 15 kg i o stare de sanatate fizic si psihic bun.</span></p>
        <p><span style={{fontWeight: 400}}>Clienii au acces n Climb House pe propria rspundere, dup ce au citit i neles Regulamentul. Accesul n sal reprezint acceptarea regulamentului nostru.&nbsp;</span></p>
        <p><span style={{fontWeight: 400}}>Pentru minorii sub 14 ani, consimtamantul de participare va fi dat de ctre printi sau nsotitorii adulti prin acordul scris.</span></p>
        <p><span style={{fontWeight: 400}}>Prin urmare, toi participanii sunt obligati s citeasc si s-i nsueasc regulile mai jos menionate</span></p>
        <ol>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Greutatea minim admis pentru participani este de 15 kg iar cea maxim este de 150 kg.&nbsp;</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Purtarea hamului este obligatorie la toate traseele mai puin la slide iar acesta va fi verificat de ctre personalul Climb House.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Purtarea ctii este obligatorie&nbsp;</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Atunci cnd traseele sunt nchise cu csuele portocalii, se ateapt ok-ul instructorului, care va asigura cratorul. La coborre se ateapt ajutorul instructorului care v va debloca carabiniera i v va indica urmtorul tarseu.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Atunci cnd triunghiurile de blocare sunt pe podea cu faa galben n sus, nu se staioneaz pe ele. Poziia triunghiurilor galbene pe podea indic faptul c o alt persoan se catar pe traseu iar o coborre a celui care se catara peste cel care stationeaza n aceasta zon ar putea conduce la accidentari.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Podeaua roie marcheaz de asemnenea zona unde nu se staioneaz.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Echiparea si dezechiparea se face numai de catre personalul Climb House.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Accesul la sala de crare se face doar in prezenta personalului Climb House.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>La crare pozitia corzii de asigurare trebuie sa fie intre brate.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Carabiniera cu coarda de asigurare va fi montat i demontat doar de catre instructorii Climb House.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>La crare este strict interzisa traversarea traseelor pe orizontala. Crtorii trebuie s se rezume la traseul de crare ales.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>n cazul n care nu veti respecta regulile prezentate la instructaj de ctre personalul autorizat, vei fi oprit din activitate.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Participantilor nu li se returneaz sumele achitate dac sunt oprii din activitate din cauza nerespectrii regulilor.&nbsp;</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>n cazul distrugerii echipamentului de protectie (ham, casc) contravaloarea acestuia va fi suportat de ctre persoana care l-a nchiriat sau de ctre parinti/nsotitori.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>In situatia in care, dupa ce a nceput sesiunea de crat, participantul considera c nu mai poate participa la activitatile sportive, suma achitat nu se returneaz.</span></li>
          <li style={{fontWeight: 400}}><span style={{fontWeight: 400}}>Toti participanii trebuie s contribuie prin actiunile si atitudinea lor la sigurana i buna desfurare a activitii. Accidentarea unui cartor de ctre un altul este responsabilitatea direct a celor implicati, instructorul/societatea fiind exonerat(a) de orice rspundere.</span></li>
        </ol>
        <p><span style={{fontWeight: 800}}>Am citit i neles Regulamentul. Suntei de accord cu el?</span></p>
        
        
      </div>
               </span>
                
            
            </div>
            
            
    </Col>
    
  </Row>
  <Row hidden={!agree} style={{padding:'10px'}}>
    <CreateClient refresh={refresh} style={{marginLeft:'5px'}}/>
  </Row>
  <hr></hr>
  <span >
  <Button variant="success" size='1.2em' onClick={()=>{SetAgree(true)}}>SUNT de accord.</Button>
  <Button variant="danger" size='1.2em' onClick={()=>{SetAgree(false)}}>NU sunt de accord.</Button>
  </span>
  
  <hr></hr>
  
</Container>
            
    )
}
export default ClientSideRules
